import { Component, OnInit, Input } from '@angular/core';
import { NavController } from '@ionic/angular';

@Component({
  selector: 'app-my-tollbar',
  templateUrl: './my-tollbar.component.html',
  styleUrls: ['./my-tollbar.component.scss'],
})
export class MyTollbarComponent implements OnInit {


  @Input('title') title;
  @Input('backDefault') backDefault = '/';

  public hasHistory ;

  constructor(
    public navCtr : NavController
  ) {
    this.hasHistory =  window.history.state.navigationId > 1
   }

  ngOnInit() {}




   close(){
     console.log('close')
     if(this.backDefault){
       this.navCtr.navigateForward(this.backDefault)
       return;
     }

     this.navCtr.navigateRoot('/')
   }

}
