import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import {environment} from 'src/environments/environment';


@Component({
  selector: 'app-btn-paid',
  templateUrl: './btn-paid.component.html',
  styleUrls: ['./btn-paid.component.scss'],
})
export class BtnPaidComponent implements OnInit {

  public mp : any;
  public loading = true;
  public esErrorLoadScript = false;

  @Input('preference_id') preference_id;
  @Input('textBack') textBack;

  

  @Output() onBack = new EventEmitter<any>();
  @Output() onClose = new EventEmitter<any>();

  constructor() { 
    console.log(this.onBack);
  }

  ngOnInit() {
    let script = document.createElement("script");
    script.type = "text/javascript";
    script.src = "https://sdk.mercadopago.com/js/v2";

    script.onload = () => {
      console.log('Loaded mercado pago')
      this.loading = false;
      this.initMP();
    }

    script.onerror = () => {
      console.log('Error al cargar libreria mercado pago')
      this.esErrorLoadScript = true;
      this.loading = false;
    }

    document.body.appendChild(script);
  }

  initMP(){
    this.mp = new window['MercadoPago'](environment.mp_public_key);
    this.checkStatusPayment();

  }

  checkStatusPayment(){

    this.mp.checkout({
      preference: {
          id: this.preference_id
      },
      render: {
            container: '.cho-container', // Indica el nombre de la clase donde se mostrar el botn de pago
            label: 'Pagar', // Cambia el texto del botn de pago (opcional)
      }
    })
    

  }


  hasOnClose(){
    return this.onClose.length > 0;
  }

  hasOnBack(){
    return this.onBack.length > 0;
  }


  back(){
    this.onBack.emit();
  }

  close(){
    this.onClose.emit();
  }

  isLoadMP(){
    return this.mp ? true : false ;
  }


}
