import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { loadavg } from 'os';
import { Router } from '@angular/router';

@Component({
  selector: 'app-team-statistics',
  templateUrl: './team-statistics.component.html',
  styleUrls: ['./team-statistics.component.scss'],
})
export class TeamStatisticsComponent implements OnInit {


  @Input('id') id;
  @Input('list') list;

  public modelTeam : Model;

  constructor(
    requestService : RequestService,
    public router : Router
  ) { 
    this.modelTeam = new Model('Team',requestService);
    console.log('app-team-statistics id:' , this.id)
    console.log('app-team-statistics list:' , this.list)
  }

  ngOnInit() {
    this.load();
  }

  load(){
    if(!this.list)
      if(this.id){
        this.modelTeam.api_loadAttribute(this.id,'statistics').subscribe(
          response => {
            if(response['status' ] =='success' ){
              this.list = response['Team']['statistics'];
            }
          }
        )
      }
    
  }

  ngOnChanges(changes: SimpleChanges) {
        
    //console.log('changes')
    if(changes['list']){
      console.log('change')
      //this.loadCooments();
    }

    if(changes['id']){
      this.load();
    }
    
  }

  showGames(status){
    if(status){
      this.router.navigateByUrl('/team/' + this.id + '/games/' +  status.toLowerCase());
    }
  }

}
