import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { UtilService } from 'src/app/services/util.service';
import { loadavg } from 'os';
import { IonContent } from '@ionic/angular';
import * as moment from 'moment';

@Component({
  selector: 'app-team-list-any-game',
  templateUrl: './team-list-any-game.component.html',
  styleUrls: ['./team-list-any-game.component.scss'],
})
export class TeamListAnyGameComponent implements OnInit {

  @ViewChild('ion-content') content: IonContent;


  @Input('id') id;
  @Input('games') games;

  public teamModel : Model;
  

  constructor(
    public requestService : RequestService,
    public dialogService : DialogService,
    public util : UtilService
  ) {
    this.teamModel = new Model('Team',requestService);
   }

  ngOnInit() {
    this.load();
  }

  async load(evet = null){
    if(!this.games){
      this.teamModel.api_loadAttribute(this.id,'games').subscribe(
        response => {
          this.games = response['Team']['games'];
          //console.log(this);
          this.selectNextDate();        },
        error =>{
          console.log(error);
        }
      )
    }
  }

  selectNextDate(){
    let date = this.getNextDate();
    console.log('select date ', date);
    if(date){
      setTimeout(()=>{
        this.util.scrollTo(date);
      },1000)
    }
  }

  getNextDate(){
    let ret = null;
    let date = moment.now();// this.util.setFormatMoment(new Date() , 'YYYY-MM-DD');
    this.games.forEach(element => {
      //console.log(element);
      if(!ret && moment(element.date).isAfter(date)){
        ret = element.date;
      }
      
    });
    return ret;
  }

}
