import { Component, OnInit, Input, SimpleChanges } from '@angular/core';

import * as Leaflet from 'leaflet';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { ShowPage } from 'src/app/pages/field/show/show.page';
import { ModalController } from '@ionic/angular';



@Component({
  selector: 'app-show-map',
  templateUrl: './show-map.component.html',
  styleUrls: ['./show-map.component.scss'],
})
export class ShowMapComponent implements OnInit {



  //deploy lucas

  public modelField : Model;
  @Input('field') field ;

  @Input('showDirection') showDirection  = false;

  @Input('id') id ;

  map: Leaflet.Map;
  newMarker = null;

  position = {
    lat : 0,
    lng : 0
  };

  constructor(
    private request : RequestService,
    private geolocation: Geolocation,
    private iab: InAppBrowser,
    public modalController : ModalController,


  ) { 
    this.modelField = new Model('Field',request);


  }

  async ngOnInit() {
    //this.leafletMap();
    this.loadField();
    this.getLocation()
  }

  loadField(){
    console.log('load field')
    if(this.id){
      this.modelField.api_find(this.id).subscribe(data => {
        if(data['status'] == 'success'){
          this.field = data['Field'];

          this.reset();
          this.leafletMap();
          this.refresh();
        }
      })
    }
  }


  async getLocation(){
    console.log('get position')
    await this.geolocation.getCurrentPosition({ timeout: 6000})
                        .then(resp=>{
                            console.log('resp geo', resp);
                            this.position.lat = resp.coords.latitude;
                            this.position.lng = resp.coords.longitude;
                        })
                        .catch( error => {
                          console.error('error geo ', error)
                        });


    let watch = this.geolocation.watchPosition();
    watch.subscribe((data) => {
      console.log('data geo' , data)
    });

    

    //this.position.lat = res.coords.latitude;
    //this.position.lng = res.coords.longitude

    //console.log('location ' , res)
    //alert(res.coords.latitude)
  }

  async getRoute(){
   //await this.getLocation();
    
   this.comoLlegar()
  }

  leafletMap() {

    /*this.field = { 
      name : '',
      lat : -27.603857971720405,
      lng : -55.323644024643414
    };
      

    if(navigator.geolocation){
      navigator.geolocation.getCurrentPosition((position) => {
           this.field.lat= position.coords.latitude;
          this.field.lng = position.coords.longitude;
      })
    }
*/


    this.map = Leaflet.map('mapId').setView([this.field.lat,this.field.lng], 11);

    const self = this;
    
    this.map.on("load",() => { 
      console.log('load map')
      
      this.refresh();
    
    });

    this.map.on('locationfound', (pos)=>{
      console.log('loc found ', pos)
    });
    this.map.on('locationerror', error => {
      console.log('loc error ', error)

    });

    //old
    //https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png

    //https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token=pk.eyJ1IjoibWFwYm94IiwiYSI6ImNpejY4NXVycTA2emYycXBndHRqcmZ3N3gifQ.rJcFIG214AriISLbB6B5aw
    Leaflet.tileLayer('https://tile.openstreetmap.org/{z}/{x}/{y}.png' , {
      attribution: 'futbol-alem.com',
      id: 'mapbox/streets-v11'
    }).addTo(this.map);

    

   

/*
    Leaflet.tileLayer('https://tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: 'futbol-alem.com',
    }).addTo(this.map);
*/

    var Icono = Leaflet.icon({
      iconUrl: "http://icons.iconseeker.com/png/fullsize/sport-fields/football-field.png",
      iconSize: [30, 40],
      iconAnchor: [15, 40],
      shadowSize: [35, 50],
      shadowAnchor: [0, 55],
      popupAnchor: [0, -40]});

    Leaflet.marker([this.field.lat, this.field.lng],{icon:Icono}).addTo(this.map).bindPopup(this.field.name,{permanent : true}).openPopup();
    //Leaflet.marker([34, 77]).addTo(this.map).bindPopup('Leh').openPopup();

    

    //this.loadFields();
    this.refresh();
  }



 ngOnChanges(changes: SimpleChanges) {
        
  console.log('changes')
  if(changes['field']){
    setTimeout(() => {
      if(this.map){
        this.map.off();
        this.map.remove();
      }

      this.leafletMap();
    }),
    1000
  }
  
}

refresh(){
  setTimeout(()=>{
    this.map.invalidateSize();
  },1000)
}

reset(){
  if(this.map){
    this.map.off();
    this.map.remove();
  }
}

ionViewDidLeave() {
  // Do actions here
  console.log('ionViewDidLeave component show-map')

  this.reset();
 
}

comoLlegar(){
  const browser = this.iab.create(`https://www.google.com/maps/dir/?api=1&destination=${this.field.lat},${this.field.lng}`,'',{
    fullscreen:"yes"
  } );
}

shareW(){

  let text = encodeURIComponent( `https://www.google.com/maps/dir/?api=1&destination=${this.field.lat},${this.field.lng}`);

  const browser = this.iab.create(`https://api.whatsapp.com/send?text=${text}`,'',{
    fullscreen:"yes"
  } );
}

async show(field){
  console.log('show')

 const modal = await this.modalController.create({
   component: ShowPage ,
   componentProps: { id: field.id }
 });

 modal.onDidDismiss().then(data=>{
   if(data.data && data.data['field']){
     //const field = data.data['field'];
     //this.utilArray.listUpdate(this.fields,field.id,field)
   }
 })

 return await modal.present();
}

}
