import { Component, OnInit, Input, Output,EventEmitter } from '@angular/core';
import { UtilService } from 'src/app/services/util.service';
import { Router } from '@angular/router';
import { AuthUserService } from 'src/app/services/auth-user.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { ModalController } from '@ionic/angular';
import { UtilArrayService } from 'src/app/services/util-array.service';
import { GameFormPage } from 'src/app/pages/game/game-form/game-form.page';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';

@Component({
  selector: 'app-old-next-game',
  templateUrl: './old-next-game.component.html',
  styleUrls: ['./old-next-game.component.scss'],
})
export class OldNextGameComponent implements OnInit {

  @Input('game') game;
  @Input('withTitle') withTitle = true;

  @Output() onDeleteGame = new EventEmitter<any>();
  @Output() onUpdateGame = new EventEmitter<any>();


  public modelGame : Model;

  constructor(
    public router : Router,
    public userAuth : AuthUserService,
    public dialogService : DialogService,
    public modalController: ModalController,
    public dialog : DialogService,
    public util : UtilService,
    public utilArray : UtilArrayService,
    public request : RequestService
  ) { }

  ngOnInit() {
    console.log('withTitle',this.withTitle)
    this.modelGame = new Model('Game',this.request);
  }

  get colorStatus(){

    if(this.game){
      switch(this.game.status){
        case 'Pendiente':
          return 'secondary';
        
        case 'Jugado':
            return 'success';
        
        case 'Suspendido':
              return 'danger';
      }
    }

    return 'default'
  }

  routerGoTo(game){
    if(game.status == 'Pendiente'){
      return '/games/profile/' + game.id;
    }else{
      return '/results/profile/' + game.id;
    }
  }

  async showEdit(game){
    const modal = await this.modalController.create({
      component: GameFormPage ,
      componentProps: { id: game.id }
    });

    modal.onDidDismiss().then(data=>{
      const game = data.data['game'];
      if(game){
        this.game = game;
      }
      //this.utilArray.listUpdate(this.games,game.id,game);
      //this.gameService.listUpdate(game.id,game);

    })

    return await modal.present();
   }

   confirmDelete(game){
    this.dialog.presentAlertConfirm('Confirmar','Estas seguro de eliminar este partido',e => {
      this.delete(game)
    });
   }

   delete(game){
     this.modelGame.api_delete(game.id).subscribe(data => {
       console.log(data);
       if(data['status'] == 'success'){
         this.dialog.showToast('Partido Eliminado',null,'success');
         if(this.onDeleteGame)
          this.onDeleteGame.emit(game);
         //this.gameService.listDelete(game.id);
         //this.utilArray.listDelete(this.games,game.id)
       }
     });
   }

}
